.home-page-list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.home-page-list ,li {
    list-style: none;
    margin: 1vw;
}

.home-page-list button{
    cursor: pointer;
    border: none;
    background: #1985A1;
    border: 1px solid #ADA8B6;
    color: whitesmoke;
    padding: 1vw;   
}

.bottom-btn{
    top:84.5%;
    position: absolute;
    left: 58%;
}
.bottom-btn:hover{
    color: white;
    background: #61C9A8;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s
}

h2{
    text-align: center;
    padding: 0;
    margin-top: 0;
    margin-bottom: 60px;
    background: #A53860;
    color: #FFEEDB;
}


