.contacts-page {
  position: relative;
  height: 100%; }
  .contacts-page .search-container {
    height: 40px;
    padding: 0 10px; }
  .contacts-page .contacts-container {
    max-height: 354px;
    overflow-x: auto; }
  .contacts-page .action-container {
    position: absolute;
    bottom: 20px;
    right: 10px; }
    .contacts-page .action-container a {
      padding: 5px 15px;
      border-radius: 50%;
      background: #1985A1;
      color: #FFEEDB;
      font-size: 30px; }



